*{
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600;700&display=swap');

:root{
  --principal:#272b35;
  --secundario:#F5F5F5;
  --tercer:#F61067;
  --cuarto:#962557;
}
body{
  background-color: var(--principal);
  color:var(--secundario);
}
.central{
  text-align: center;
}
.contenedor-circulo{
  width: 65vw;
  height: 65vw;
  margin: 30vw auto;
  font-size: 27vw;
  transition: font-size 0.03s linear;
}
.contenedor-circulo.small{
  
  font-size: 19vw;
  transition: font-size 0.8s ease-in-out;
}
.circulo{
  background-color:transparent;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  /*box-shadow: rgba(255,255,255,0) 0px 0px 0vw 0vw inset,0 0 12vw 0px rgba(0,0,0,0.3);*/
  box-shadow:rgba(255,255,255,0) 0px 0px 0vw 0vw inset ;
  font-weight: 600;
  user-select: none;
  border: 2vw solid var(--tercer);
  transition:box-shadow 0.2s;
  position: relative;
  box-sizing: border-box;
}
.circulo::after{
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 50%;
  z-index: -1;
  box-shadow:
  rgba(0,0,0,0.6) 0vw 6vw 10vw -5vw inset,rgba(0,0,0,0.6) 0vw 6vw 10vw -2vw ;
}
.animacion{
  transition:box-shadow 0s;
  box-shadow: rgba(255, 255, 255,0.1) 0px 0vw 0vw 32.5vw inset;
}
.contador{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 0 0 0.2rem var(--tercer) ;
  transition: text-shadow 0.2s ease;
}
.contador.pausa{
  text-shadow: 0 0 0.6rem var(--tercer) ;
}
.ms{
  font-size: 50%;
}
.contenedor-botones{
  transition: 0.3s ;
  height: 17vw;
  width: 50vw;
  margin: auto;
  position:relative;
}

.boton i{
  color: var(--secundario);
  font-size:8vw;
  line-height: 17vw;
}
.boton i.fa-play{
  
  margin-left: 2vw;
}
.boton{
  position: absolute;
  border-radius: 50%;
  width: 17vw;
  height: 17vw;
  cursor: pointer;
  border: none;
  /*background-color: var(--tercer);*/
  background:linear-gradient(30deg,var(--cuarto) ,var(--tercer));
  /*box-shadow: 0 0 5vw 0px rgba(0,0,0,0.2);*/
  box-shadow: rgba(255,255,255,0) 0px 0px 0vw 0vw inset,0 0.3rem 5vw 0px rgba(0,0,0,0.5);
  
  right: 50%;
  transform: translate(50%,0) scale(1,1);
  transition: box-shadow 0.3s, right 0.2s ease,opacity 0.2s ease, transform 0.2s ease;
  z-index: 2;
}
.stop{
  opacity: 0;
}
.vuelta{
  opacity: 0;
  z-index: 1;
}
.derecha{
  transform: translate(50%,0) scale(0.7,0.7);
  right: 0%;
  opacity: 1;
}
.izquierda{
  transform: translate(50%,0) scale(0.7,0.7);
  right: 100%;
  opacity: 1;
}
.transparente{
  opacity: 0;
}
.animacion-boton{
  box-shadow: rgba(255,255,255,0.35) 0px 0vw 0vw 8.5vw inset,0 0 5vw 0px rgba(0,0,0,0.5);
  transition: 0s;
}
button{
  outline: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
@media only screen and (min-width: 768px) {
  .contenedor-circulo{
    width: 25rem;
    height: 25rem;
    margin:0;
  }
  .boton{
    margin:0;
  }
}